<template>
  <div class="houseRental">
    <navigation-bar  @onLeftClick="goBack" pageName="房产租售" />
    <div class="box">
      <div class="item" v-for="(item, index) in list" :key="index" @click="goPage(item)">
          <div class="img-box">
              <img :src="item.icon" alt="">
          </div>
          <div class="name">{{item.name}}</div>
      </div>
    </div>  
  </div>
</template>

<script>
import buyHouseIcon from "@/assets/images/home/houseRental/buyHouse.png";
import rentHouseIcon from "@/assets/images/home/houseRental/rentHouse.png";
import sendHouseIcon from "@/assets/images/home/houseRental/sendHouse.png";
import myHouseIcon from "@/assets/images/home/houseRental/myHouse.png";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "houseRental",
  data() {
    return {
      list: [
        {
          icon: buyHouseIcon,
          name: "我要买房",
          route: "buyHouse"
        },
        {
          icon: rentHouseIcon,
          name: "我要求租",
          route: "rentHouse"
        },
        {
          icon: sendHouseIcon,
          name: "发布房源",
          route: "sendHouse"
        },
        {
          icon: myHouseIcon,
          name: "我的发布",
          route: "myHouse"
        }
      ]
    };
  },
  components: {
    NavigationBar
  },
  created() {},
  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      this.$router.go(-1);
    },

    /**
     * 页面跳转
     */
    goPage(item) {
      this.$store.dispatch("setReload", true);
      this.$store.dispatch("setCity", {
        cityName: "",
        cityCode: ""
      });
      this.$store.dispatch("setAreaName", "");
      this.$store.dispatch("setReloadMine", true);
      this.$store.dispatch("setMineActive", 0);
      this.$router.push({ name: item.route });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.houseRental {
  width: 100%;
  height: 100%;
  background: #fff;
  .box {
    padding-top: 24px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    .item {
      width: 50%;
      height: 80px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-direction: column;
      margin-bottom: 30px;
      .img-box {
        width: 54px;
        height: 54px;
        img {
          width: 54px;
          height: 54px;
        }
      }
      .name {
        width: 100%;
        font-size: 14px;
        text-align: center;
        line-height: 20px;
      }
    }
  }
}
</style>
